import React, { Component } from 'react';

//images
import Profile from '../assets/creighbattle-clipped.png'

class AboutSection extends Component {

    goSocial(site) {
        if (site === "facebook") {
            window.open("https://www.facebook.com/creigh.battle", '_blank');
        } else if (site === "twitter") {
            window.open("https://twitter.com/creigh_battle", '_blank');
        } else {
            window.open("https://www.instagram.com/creighbattle/?hl=en", '_blank');
        }
    }


    render() {
        return (
            <div>
                <div className="about" id="about">
                    <div className="container about-test">
                        <div className="profile-img" data-aos="fade-right" data-aos-delay="600" data-aos-duration="4000">
                            <img src={Profile} alt=""/>
                        </div>
                        <div className="about-details" data-aos="fade-left" data-aos-delay="600" data-aos-duration="4000">
                            <div className="about-heading">
                                <h1>About</h1>
                                <h6>Myself</h6>
                            </div>
                            <p>
                                Hello and welcome to my page. I am a web designer/developer focused
                                on creating user friendly web experiences. Over the past four years at college, 
                                my interest for web development grew significantly. Although developing can be frustrating
                                at times, I love the satisification of working through my problems and figuring out a 
                                soultion. <br/> <br/>
                                I'm very excited and eager to start sharing some of my work with the world!
                            </p>
                            <div className="social-media">
                                <ul className="nav-list">
                                    <li>
                                        <p className="icon-link" onClick={() => this.goSocial("facebook")}>
                                            <i className="fab fa-facebook-square"></i>
                                        </p>
                                    </li>
                                    <li>
                                        <p className="icon-link" onClick={() => this.goSocial("twitter")}>
                                            <i className="fab fa-twitter-square"></i>
                                        </p>
                                    </li>
                                    <li>
                                        <p className="icon-link" onClick={() => this.goSocial("instagram")}>
                                            <i className="fab fa-instagram"></i>
                                        </p>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default AboutSection;